@import url(https://fonts.googleapis.com/css2?family=Gluten:wght@200;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gluten:wght@200;400;500;600;800&display=swap);
body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif,
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#img{
    
    padding-left: 30px;
    padding-top: 19px;
    padding-bottom: 19px;
    background-color: #f7f7f7;
    /* display: flex;
    justify-content: center;
    align-items: center; */
}
#makeup-image{
    width: 200px;
    height: 60px;
    
}

#content{
    /* display: grid; */
    min-height: 100vh;
    background-color: #F6EFEE;
}

#filter-by-brand{
    display: flex;
    flex-direction: column;
    border-top: rgb(223, 223, 223) 1px solid;
}
#items{
    display: grid;
    grid-gap: 16px; 
    grid-template-columns: auto auto auto auto;
    padding-right: auto;
    padding-left: auto; 
    padding-top:56px ;
    justify-content: center;
}
@media screen and (max-width:1500px){
    #items {
        display: grid;
        grid-gap: 16pxpx;
        grid-template-columns:auto auto auto;
        grid-gap: 16px;
        width: 100%;
        align-content: center;
    }
}

@media screen and (max-width:1100px){
    #items {
        display: grid;
        grid-gap: 16px;
        grid-template-columns:auto auto;
        width: 100%;
        align-content: center;
    }
}
@media screen and (max-width:725px){
    #items {
        display: grid;
        grid-gap: 16px;
        grid-template-columns:auto ;
        width: 100%;
        align-content: center;
    }
}

#filter{
    margin-left: 94px;
}
#filter-title{
    font-family: Gluten;
    font-size: 30px;
    font-weight: bold;
    color: #35201C;
    margin-bottom: 40px;
   
}
#filter-title:hover{
    color: #6A4139;
}
.checkbox{
    width: 20px;
    height: 20px;
}
.checkbox:hover{
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.brands{
    font-family: Gluten;
    font-size: 20px;
    font-weight: normal;
    color: #192024;
    margin-left: 10px;
    margin-right: 24px;
    vertical-align: text-top;
    

}
@media screen and (max-width:550px){
    .brands{
    font-family: Gluten;
        font-size: 13px;
        font-weight: normal;
        color: #192024;
        margin-left: 10px;
        margin-right: 24px;
        vertical-align: text-top;
    }


}
@font-face {
    font-family: HelveticaNeue;
    src: url(/makeupproduct/static/media/HelveticaNeue.372d58a9.ttf);
}

#item{
    position: relative;
    width: 350px;
    height: 360px;
    border-radius: 8px;
    background-color: #ffffff;
    /* border: solid 1px #363f45 ; */
    box-shadow: 0 -1px 4px 0 rgba(25,32,36,0.04), 0 3px 6px 0 rgba(25,32,36,0.16);
}
#logo{
    border-radius: 5px;
    /* box-shadow: rgba(33, 35, 38, 0.1) 0px 10px 10px -10px; */
    width: 220px;
    height: 220px;
}

#product-logo{
    display: flex;
    justify-content: center;
}

#product-name{
    font-family: HelveticaNeue;
    font-size: 16px;
    font-weight: bold;
    color: #102024;
    display: inline-flex;
    text-align: center;
    justify-content: center;
}
#product-name-limit{
    width: 300px;
    font-family: HelveticaNeue;
    font-size: 16px;
    font-weight: bold;
    color: #102024;
    /* vertical-align: baseline; */
    display: inline-flex;
    text-align: center;
    justify-content: center;

}

#product-name::first-letter{
    font-size: 16px;
}

#product-info{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

}

.item-hover{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
    font-weight: normal;
    color: #192024;
    display: none;
    padding-top: 2px;
    padding-bottom: 2px;
}
#item:hover .item-hover{
    display: block;
}

#item:hover{
    /* border: solid 1px #363f45 ; */
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}


#info{
    display: flex;
    flex-direction: column;
    padding-top: 18px;
    min-height: inherit;
    width: -webkit-max-content;
    width: max-content;
    justify-content: center;
    text-align: center;
}

#title{
    font-family: Gluten;
    font-weight: 900;
    font-size: 50px;
    color: #35201C;
    padding-left: 94px;
    padding-top: 67px;
    padding-bottom: 73px;
    /* text-align: center; */
}

#title:hover{
    color: #6A4139;
}
